.row{
    margin-left: 20px;
    color: white;
}
.row__posters{
    display: flex;
    overflow-y: hidden;
    overflow-x: scroll;
    padding: 20px;
    width: 100%;
}

.row__posters::-webkit-scrollbar {
    display: none;
}
.row__poster {
    object-fit: contain;
    width: 100%;
    max-height: 100px;
    transition: transform 450ms;
    margin-right: 10px;
   
}

.row__poster:hover {
    transform: scale(1.08);
    opacity: 1;
}

.row__posterLarge {
    max-height: 250px;
    object-fit: contain;
    width: 100%;
    transition: transform 450ms;
    margin-right: 10px;
}

.row__posterLarge:hover{
    transform: scale(1.09);
    opacity: 1;
}